* {
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	max-width: 100%;
}
@font-face {
	font-family: headerFont;
	src: url(./../static/fonts/font.0899368.ttf);
}
.row::after {
	content: "";
	clear: both;
	display: block;
}
[class*="col-"] {
	float: left;
}
.col-1 {width: 8.33%;
}
.col-2 {width: 16.66%;
}
.col-3 {width: 25%;
}
.col-4 {width: 33.33%;
}
.col-5 {width: 41.66%;
}
.col-6 {width: 50%;
}
.col-7 {width: 58.33%;
}
.col-8 {width: 66.66%;
}
.col-9 {width: 75%;
}
.col-10 {width: 83.33%;
}
.col-11 {width: 91.66%;
}
.col-12 {width: 100%;
}
html {
	height: 100%;
}
body {
	font-size: 90%;

	color: #333;
	font-family: Arial, Helvetica, sans-serif;
	
	text-shadow: 1px 1px 1px rgba(0,0,0,0.004);
	/*text-rendering: optimizeLegibility !important;*/
    -webkit-font-smoothing: antialiased !important;

	margin: 0;
	padding: 0;

	background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.3)),to(rgba(0, 0, 0, 0.3))),
		url(./../static/img/bakgrunn.0761e86.jpg);

	background: linear-gradient(rgba(0, 0, 0, 0.3),rgba(0, 0, 0, 0.3)),
		url(./../static/img/bakgrunn.0761e86.jpg);
	//background-color: rgba(192, 48, 48, 1);
}
ul.topnav {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
	background-color: #333;

	/* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#5a647f+0,3f4c6b+100 */
	background: #5a647f; /* Old browsers */ /* FF3.6-15 */ /* Chrome10-25,Safari5.1-6 */
	background: -webkit-gradient(linear,	left top, left bottom,	from(#5a647f),to(#3f4c6b));
	background: linear-gradient(to bottom,	#5a647f 0%,#3f4c6b 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#5a647f', endColorstr='#3f4c6b',GradientType=0 ); /* IE6-9 */
}
ul.topnav li {float: left;
}
ul.topnav li a {
	display: inline-block;
	color: #f2f2f2;
	text-align: center;
	padding: 14px 16px;
	text-decoration: none;
	-webkit-transition: background-color 250ms;
	transition: background-color 250ms;
	font-size: 17px;
}
ul.topnav li a:hover {
	background-color: rgba(255, 255, 255, 0.1);
}
ul.topnav li.icon {display: none;
}
/* Side setup */
header {
	position: relative;
	height: 7em;
	padding: 15px;
	background-color: #fff;
	text-align: center;
}
/* Header Border */
header:after {
	z-index: -1;
	content: ""; 
	position: absolute; 
	bottom: 1px;
	left: -1px;
	width: 100%; 
	height: 1px;
	-webkit-box-shadow: 0px 5px 15px rgba(0,0,0,1);
	        box-shadow: 0px 5px 15px rgba(0,0,0,1);
}
#content_wrapper {
	margin: 0 15%;
	-webkit-box-shadow: 5px 5px 5px rgba(0,0,0,0.004);
	        box-shadow: 5px 5px 5px rgba(0,0,0,0.004);

	-webkit-transition: padding 250ms, margin 250ms; /* Safari */
	transition: padding 250ms, margin 250ms;
}
/* LoginTable */
#loginTable {
	background-color: rgba(51, 119, 170, 0.5);
	-webkit-box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
	        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
	color: #333;
	z-index: 99;

	-webkit-transition: background-color 250ms, box-shadow 250ms;
	-webkit-transition: background-color 250ms, -webkit-box-shadow 250ms;
	transition: background-color 250ms, -webkit-box-shadow 250ms;
	transition: background-color 250ms, box-shadow 250ms;
	transition: background-color 250ms, box-shadow 250ms, -webkit-box-shadow 250ms;
}
#loginTable:hover {
	background-color: #bbb;
	-webkit-box-shadow: 2px 2px 5px rgba(131, 183, 221, 0.3);
	        box-shadow: 2px 2px 5px rgba(131, 183, 221, 0.3);
}
#loginTable td {
	padding: 5px;
}
#btnLogin {
	height: 100%;
}
#loginBox {
	float: left;
}
/* END */
#logo_text {
	text-transform: uppercase; 
	color: #333;
	/* text-shadow: 0.05em 0.05em 0.05em rgba(0, 0, 0, 0.4); */

	-ms-user-select: none;

	    user-select: none;
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -o-user-select: none;
    cursor: default;

    font-family: headerFont;
    font-size: 4em;
}
nav {
	-webkit-box-shadow: 0px 5px 10px rgba(255, 255, 255, 0.3);
	        box-shadow: 0px 5px 10px rgba(255, 255, 255, 0.3);
}
#content_row {
	background-color: rgba(255, 255, 255, 0.85);
	-webkit-box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
	        box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
	border-left: 1px solid black;
}
main {
	padding: 1em 1em;
	font-size: 120%;
}
aside {
	font-size: 90%;
	margin: auto;
	background-color: rgba(255, 255, 255, 0.7);
	-webkit-box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.5);
	        box-shadow: 2px 2px 15px rgba(0, 0, 0, 0.5);
	padding: 10px;
	overflow: hidden;

	text-align: center;
}
aside #subjectContainer {
	margin-top: 15px;
	border-top: 1px solid #aaa;
}
aside .subjectSection {
	border-bottom: 1px solid #ccc;
}
aside .subjectSection .subjectList {
	font-size: 120%;
	text-align: left;
	float: left;
	list-style-type: decimal;
}
aside h2,h3 {
	margin-bottom: 0px;
}
aside .subjectList li {
	margin-bottom: 1em;
	text-decoration: none;
}
aside .clr {
	clear:both;
	font-size: 0;
}
#footer_wrap {
	padding: 1.5em;
	text-align: center; 
	margin: auto;
}
footer {
	color: rgba(255, 255, 255, 0.95);
	text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}
/*
	Extra:
*/
#profilbilde {
	width: 15em;
	margin: auto;
	-webkit-box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
	        box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.5);
}
td {
	padding: 0.2em;
}
/* END Extra */
/* Resize */
@media only screen and (max-width : 1060px) {
#content_wrapper {
		margin: 0;
		padding: 0;
		font-size: 0.8em;
}
#logo_text {
		font-size: 4em;
}
}
/* iPhones */
@media only screen and (max-width : 800px) {
#logo_text {
		font-size: 3em;
}
aside {
		clear: both;
		width: 100%;
}
#article_row article,#article_row aside {
		clear: both;
		width: 100%;
}
#profilbilde {
		width: 10em;
}
#content_wrapper {
		padding: 0;
		font-size: 0.7em;
}
}
/* iPhones */
@media only screen and (max-width : 600px) {
body {
		margin: 0;
		padding: 0;
}
header {
		position: static;
		height: 51px;
		background-color: white;
}
#content_wrapper {
		padding: 0;
		font-size: 0.5em;
}
[class*="col-"] {
		clear: both;
		width: 100%;
}
ul.topnav li:not(:first-child) {display: none;
}
ul.topnav li.icon {
		float: right;
		display: inline-block;
}
ul.topnav.responsive {position: relative;
}
ul.topnav.responsive li.icon {
		position: absolute;
		right: 0;
		top: 0;
}
ul.topnav.responsive li {
		float: none;
		display: inline;
}
ul.topnav.responsive li a {
		display: block;
		text-align: left;
}
#loginBox table,tbody,thead,tr,td,th { 
		display: block;
		max-width: 100%;
		padding: 0.1em;
}
#loginBox input {
		max-width: 100%;
}
#loginBox {
		width: 100%;
		display: inline-block;
}
#logo_text {
		display: none;
}
header {
		padding: 0;
}
aside {
		padding: 5%;
}
}




































































